<template>
    <div class="product">
        <table class="table">
            <thead>
                <tr class="danger">
                    <th>编号</th>
                    <th>名称</th>
                    <th>价格</th>
                    <th>数量</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(shop,index) in shopList" :key="index">
                    <td>{{ shop.id }}</td>
                    <td>{{ shop.title }}</td>
                    <td>{{ shop.price }}</td>
                    <td>{{ shop.num }}</td>
                    <td>
                        <button type="button" class="btn btn-danger" @click="addTocart(shop)">加入购物车</button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
    name : 'product',
    computed:{
        ...mapGetters(['shopList'])
    },
    methods:{
        ...mapActions(['addTocart'])
    }
}
</script>


