<template>
    <div class="cart">
        <h2>我的购物车</h2>
        <p v-if="!cartList.length">购物车空空如也~</p>
        <table class="table" v-if="cartList.length">
            <thead>
                <tr class="danger">
                    <th>编号</th>
                    <th>名称</th>
                    <th>价格</th>
                    <th>数量</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in cartList" :key="index">
                    <td>{{ item.id }}</td>
                    <td>{{ item.title }}</td>
                    <td>{{ item.price }}</td>
                    <td>{{ item.num }}</td>
                    <td>
                        <button type="button" class="btn btn-success" @click="delTr(item)">删除</button>
                    </td>
                </tr>
            </tbody>
            <tfoot>
                <tr class="danger">
                    <th>总数：</th>
                    <th>{{ totalNum }}</th>
                    <th>总价：</th>
                    <th>{{ totalPrice }}</th>
                    <th>
                        <button type="button" class="btn btn-info" @click="clearAll">清空</button>
                    </th>
                </tr>
            </tfoot>
        </table>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
    name : 'cart',
    computed:{
        ...mapGetters(['shopList','cartList','totalNum','totalPrice'])
    },
    methods:{
        ...mapActions(['clearAll','delTr'])
    }
}
</script>


