const state = {
    shopList:[
        {
            "id":"001",
            "title":"iPhone7",
            "price":"5000",
            "num":"10"
        },
        {
            "id":"002",
            "title":"iPhone8",
            "price":"7000",
            "num":"120"
        },
        {
            "id":"003",
            "title":"小米",
            "price":"1999",
            "num":"18"
        },{
            "id":"004",
            "title":"华为",
            "price":"3888",
            "num":"2"
        },
        {
            "id":"005",
            "title":"三星",
            "price":"4000",
            "num":"43"
        }
    ],
    //存放已加入购物车的商品
    added:[]
}
const getters = {
   shopList(state){
       return state.shopList
   },
   cartList(state){
       return state.added.map(({id,num}) => {
           //购物车中的商品
           let cartProducts = state.shopList.find( n => n.id === id);
           return {
               ...cartProducts,
               num
           }
       });
   },
   totalNum(state,getters){
        let num = 0;
        getters.cartList.forEach(n => {
            num += n.num
        });
        return num;
   },
   totalPrice(state,getters){
        let price = 0;
        getters.cartList.forEach( n => {
            price += n.num * n.price
        });
        return price;
   }
}
const mutations = {
    add(state,{id}){
        //查看该商品是否已经收藏过
        let records = state.added.find( n => n.id === id);
        if(!records){
            state.added.push({
                id,
                num:1
            })
        }else{
            records.num++
        }
    },
    clear(state){
        state.added = []
    },
    del(state,product){
       state.added.forEach((n,i) => {
           if(n.id === product.id){
               state.added.splice(i,1)
           }
       }); 
    }
}
const actions = {
    addTocart({ commit },product){
        commit('add',{
            id:product.id
        })
    },
    clearAll({ commit }){
        commit('clear')
    },
    delTr({ commit },product){
        commit('del',{
            id:product.id
        })
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}


